  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --dark-black: #171717;
  --eerie-black: #262626;
  --french-grey: #d1d5db;
  --sea-salt: #fafafa;
  --whitesmoke: #f5f5f5;
  --timberwolf: #d4d4d4;
  --platinum: #e5e5e5;
  --orange: #c2410c;
  --shamrock-green: #059669;
}

html,
body {
  height: 100%;
}

html {
  font-size: 62.5%;
  line-height: 1.6;
}

body {
  font-family: "Poppins", sans-serif, system-ui, "Segoe UI", Roboto, Helvetica,
    Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

.material-symbols-outlined {
  display: flex;
  align-items: center;
  font-variation-settings:
    "FILL" 0,
    "wght" 400,
    "GRAD" 0,
    "opsz" 24;
  font-size: 2.2rem;
}

.material-symbols-outlined-active {
  font-variation-settings: "FILL" 1;
}

.disabled {
  display: none !important;
}

.app-container {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 5fr;
}

.sidebar {
  background-color: var(--eerie-black);
  color: white;
}

.sidebar-nav {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, max-content);
  gap: 30px;
  padding: 30px 20px;
}

.app-logo {
  display: flex;
  justify-content: center;
  font-size: 3rem;
  font-weight: 700;
  background: linear-gradient(
    90deg,
    #f4ddff -3.91%,
    #cdd5ef 39.6%,
    #ffdcdc 67.65%,
    #bdeee5 106%
  );
  -webkit-background-clip: text;
  color: transparent;
}

.workspace-header {
  display: flex;
  justify-content: space-between;
  font-size: 2rem;
  margin: 0 10px;
  font-weight: 600;
  color: var(--sea-salt);
}

.menus-section,
.workspaces {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
}

.menus-section {
  margin-bottom: 50px;
}

.workspaces {
  margin-top: 30px;
}

.project-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  padding: 10px 15px;
  cursor: pointer;
  color: var(--platinum);
  font-weight: 600;
}

.project-card-active {
  background-color: var(--dark-black);
  color: var(--sea-salt);
  font-weight: 650;
  scale: 1.03;
}

.project-nav {
  font-size: 1.8rem;
}

.project-icon {
  height: 24px;
  width: 24px;
}

.content {
  height: 100%;
  position: relative;
  display: grid;
  grid-template-rows: max-content 6fr max-content;
  background-color: var(--dark-black);
}

.display-board {
  position: relative;
}

.display-banner {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  width: 100%;
  height: 300px;
}

.display-project-name {
  background: rgba(229, 231, 235, 0.5);
  padding: 20px 50px;
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 2.6rem;
  font-weight: 700;
  color: var(--dark-black);
  border-radius: 5px;
}

.tasks-section {
  display: grid;
  grid-template-rows: repeat(3, max-content);
  padding: 80px 250px;
}

.tasks-container,
.tasks-completed-container {
  display: flex;
  flex-direction: column;
  width: auto;
  font-size: 1.8rem;
}

.task-form {
  display: grid;
  grid-template-rows: max-content 1fr;
  grid-template-columns: 1fr 1fr;
  background-color: var(--platinum);
  margin-top: 20px;
  font-size: 1.8rem;
  border-radius: 5px;
}

.task-form input[type="text"],
input[type="date"],
textarea,
select {
  border: 2px solid var(--eerie-black);
  border-radius: 5px;
  font-family: inherit;
  font-size: 1.8rem;
  background-color: var(--whitesmoke);
}

.task-form textarea {
  resize: none;
  padding: 2px 5px;
}

.top-panel {
  grid-column: 1/3;
  display: grid;
  grid-template-columns: 1fr 1fr;
  color: var(--eerie-black);
  background-color: var(--platinum);
  gap: 100px;
  padding: 20px;
}

.bottom-panel {
  grid-column: 1/3;
  grid-row: 2/3;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 0px 20px 20px;
  gap: 100px;
}

.left-panel {
  grid-column: 1/2;
  display: grid;
  grid-template-rows: max-content;
  gap: 10px;
}

.right-panel {
  grid-column: 2/3;
  display: grid;
  grid-template-rows: repeat(3, max-content);
  gap: 10px;
}

.action-panel {
  display: flex;
  justify-content: end;
  gap: 20px;
  margin-top: 10px;
}

.top-panel > .card-right {
  justify-content: end;
}

.card-left > input {
  background-color: inherit;
  padding: 2px 5px;
  width: 100%;
}

.task-card {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: transparent;
  color: var(--sea-salt);
  padding: 20px 20px;
  font-weight: 500;
  margin-top: 20px;
  border: 2px solid var(--platinum);
  border-radius: 5px;
}

.card-left,
.card-right {
  display: flex;
  align-items: center;
  gap: 15px;
}

.card-right > span {
  color: transparent;
}

.task-check {
  height: 24px;
  width: 24px;
  background-color: transparent;
  border-radius: 5px;
  border: 3px solid var(--platinum);
  cursor: pointer;
}

.task-checked {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--orange);
}

.task-done p {
  text-decoration: line-through;
  color: gray;
}

.content-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  position: sticky;
  bottom: 0;
  font-size: 1.8rem;
  width: 100%;
  padding: 10px;
  background: rgba(38, 38, 38, 0.5);
}

.done-count {
  font-size: 1.8rem;
  padding: 5px;
}

.dialog-modal {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: none;
  border-radius: 10px;
  background-color: var(--french-grey);
}

.form-header {
  display: flex;
  background-color: var(--eerie-black);
  align-items: center;
  justify-content: space-between;
  padding: 5px;
}

.form-header > p {
  color: var(--sea-salt);
  font-size: 1.9rem;
  font-weight: 700;
}

.form-body {
  padding: 15px 10px;
}

.form-body > p {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 5px;
}

#projectNameInput {
  font-family: inherit;
  font-size: 1.6rem;
  padding: 5px;
  width: 420px;
  border: none;
  border-radius: 5px;
  margin-bottom: 10px;
}

.project-icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.radio-label {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  background-color: var(--whitesmoke);
  border-radius: 5px;
}

.radio-label input {
  display: none;
}

.radio-label > span {
  padding: 5px;
  border-radius: 5px;
}

.radio-label input:checked + span {
  border: 2px solid var(--eerie-black);
}

.priority-indicator {
  position: absolute;
  top: 0;
  left: -1px;
  width: 7px;
  height: calc(100% + 2px);
  border-radius: 4px 0 0 4px;
}

.cancel-btn,
.action-btn {
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  background-color: var(--timberwolf);
  color: var(--eerie-black);
  width: 100px;
}

.action-btn {
  background-color: var(--shamrock-green);
  color: var(--sea-salt);
}

.action-btn,
.addTask-btn,
.cancel-btn,
#showDoneTasksBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: inherit;
  font-size: 1.8rem;
  font-weight: 700;
  cursor: pointer;
}

.addTask-btn {
  justify-content: start;
  background-color: var(--timberwolf);
  width: 100%;
  border: none;
  color: var(--eerie-black);
  padding: 20px 20px;
  margin-top: 30px;
  gap: 15px;
  border-radius: 5px;
}

.create-project-btn,
#showDoneTasksBtn {
  display: flex;
  align-items: center;
  border: none;
  background-color: transparent;
  cursor: pointer;
  color: var(--platinum);
  gap: 10px;
  padding: 5px;
  border-radius: 5px;
}

#showDoneTasksBtn:hover {
  background-color: var(--orange);
  cursor: pointer;
}

.task-card:hover {
  background-color: var(--timberwolf);
  color: var(--eerie-black);
}

.task-card:hover .task-check {
  border-color: var(--eerie-black);
}

.project-card:hover,
.create-project-btn:hover {
  background-color: var(--dark-black);
}

.project-card:hover .edit-btn,
.task-card:hover .edit-btn {
  color: #0369a1;
  cursor: pointer;
}

.project-card:hover .delete-btn,
.task-card:hover .delete-btn {
  color: #dc2626;
  cursor: pointer;
}

.edit-btn:hover,
.delete-btn:hover {
  scale: 1.1;
}

.cancel-btn:hover,
.action-btn:hover {
  scale: 1.05;
}

.task-form input[type="text"]:focus,
textarea:focus,
select:focus {
  background-color: var(--eerie-black);
  color: var(--sea-salt);
}

.dialog-modal::backdrop {
  backdrop-filter: blur(2px);
}
