nus-section {\n  margin-bottom: 50px;\n}\n\n.workspaces {\n  margin-top: 30px;\n}\n\n.project-card {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  border-radius: 10px;\n  padding: 10px 15px;\n  cursor: pointer;\n  color: var(--platinum);\n  font-weight: 600;\n}\n\n.project-card-active {\n  background-color: var(--dark-black);\n  color: var(--sea-salt);\n  font-weight: 650;\n  scale: 1.03;\n}\n\n.project-nav {\n  font-size: 1.8rem;\n}\n\n.project-icon {\n  height: 24px;\n  width: 24px;\n}\n\n.content {\n  height: 100%;\n  position: relative;\n  display: grid;\n  grid-template-rows: max-content 6fr max-content;\n  background-color: var(--dark-black);\n}\n\n.display-board {\n  position: relative;\n}\n\n.display-banner {\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n  height: 100%;\n  width: 100%;\n  height: 300px;\n}\n\n.display-project-name {\n  background: rgba(229, 231, 235, 0.5);\n  padding: 20px 50px;\n  position: absolute;\n  bottom: -30px;\n  left: 50%;\n  transform: translateX(-50%);\n  font-size: 2.6rem;\n  font-weight: 700;\n  color: var(--dark-black);\n  border-radius: 5px;\n}\n\n.tasks-section {\n  display: grid;\n  grid-template-rows: repeat(3, max-content);\n  padding: 80px 250px;\n}\n\n.tasks-container,\n.tasks-completed-container {\n  display: flex;\n  flex-direction: column;\n  width: auto;\n  font-size: 1.8rem;\n}\n\n.task-form {\n  display: grid;\n  grid-template-rows: max-content 1fr;\n  grid-template-columns: 1fr 1fr;\n  background-color: var(--platinum);\n  margin-top: 20px;\n  font-size: 1.8rem;\n  border-radius: 5px;\n}\n\n.task-form input[type=\"text\"],\ninput[type=\"date\"],\ntextarea,\nselect {\n  border: 2px solid var(--eerie-black);\n  border-radius: 5px;\n  font-family: inherit;\n  font-size: 1.8rem;\n  background-color: var(--whitesmoke);\n}\n\n.task-form textarea {\n  resize: none;\n  padding: 2px 5px;\n}\n\n.top-panel {\n  grid-column: 1/3;\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  color: var(--eerie-black);\n  background-color: var(--platinum);\n  gap: 100px;\n  padding: 20px;\n}\n\n.bottom-panel {\n  grid-column: 1/3;\n  grid-row: 2/3;\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  padding: 0px 20px 20px;\n  gap: 100px;\n}\n\n.left-panel {\n  grid-column: 1/2;\n  display: grid;\n  grid-template-rows: max-content;\n  gap: 10px;\n}\n\n.right-panel {\n  grid-column: 2/3;\n  display: grid;\n  grid-template-rows: repeat(3, max-content);\n  gap: 10px;\n}\n\n.action-panel {\n  display: flex;\n  justify-content: end;\n  gap: 20px;\n  margin-top: 10px;\n}\n\n.top-panel > .card-right {\n  justify-content: end;\n}\n\n.card-left > input {\n  background-color: inherit;\n  padding: 2px 5px;\n  width: 100%;\n}\n\n.task-card {\n  position: relative;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  background-color: transparent;\n  color: var(--sea-salt);\n  padding: 20px 20px;\n  font-weight: 500;\n  margin-top: 20px;\n  border: 2px solid var(--platinum);\n  border-radius: 5px;\n}\n\n.card-left,\n.card-right {\n  display: flex;\n  align-items: center;\n  gap: 15px;\n}\n\n.card-right > span {\n  color: transparent;\n}\n\n.task-check {\n  height: 24px;\n  width: 24px;\n  background-color: transparent;\n  border-radius: 5px;\n  border: 3px solid var(--platinum);\n  cursor: pointer;\n}\n\n.task-checked {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: var(--orange);\n}\n\n.task-done p {\n  text-decoration: line-through;\n  color: gray;\n}\n\n.content-footer {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  position: sticky;\n  bottom: 0;\n  font-size: 1.8rem;\n  width: 100%;\n  padding: 10px;\n  background: rgba(38, 38, 38, 0.5);\n}\n\n.done-count {\n  font-size: 1.8rem;\n  padding: 5px;\n}\n\n.dialog-modal {\n  position: relative;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  border: none;\n  border-radius: 10px;\n  background-color: var(--french-grey);\n}\n\n.form-header {\n  display: flex;\n  background-color: var(--eerie-black);\n  align-items: center;\n  justify-content: space-between;\n  padding: 5px;\n}\n\n.form-header > p {\n  color: var(--sea-salt);\n  font-size: 1.9rem;\n  font-weight: 700;\n}\n\n.form-body {\n  padding: 15px 10px;\n}\n\n.form-body > p {\n  font-size: 1.8rem;\n  font-weight: 600;\n  margin-bottom: 5px;\n}\n\n#projectNameInput {\n  font-family: inherit;\n  font-size: 1.6rem;\n  padding: 5px;\n  width: 420px;\n  border: none;\n  border-radius: 5px;\n  margin-bottom: 10px;\n}\n\n.project-icons {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n}\n\n.radio-label {\n  display: inline-flex;\n  align-items: center;\n  cursor: pointer;\n  background-color: var(--whitesmoke);\n  border-radius: 5px;\n}\n\n.radio-label input {\n  display: none;\n}\n\n.radio-label > span {\n  padding: 5px;\n  border-radius: 5px;\n}\n\n.radio-label input:checked + span {\n  border: 2px solid var(--eerie-black);\n}\n\n.priority-indicator {\n  position: absolute;\n  top: 0;\n  left: -1px;\n  width: 7px;\n  height: calc(100% + 2px);\n  border-radius: 4px 0 0 4px;\n}\n\n.cancel-btn,\n.action-btn {\n  border: none;\n  padding: 5px 10px;\n  border-radius: 5px;\n  background-color: var(--timberwolf);\n  color: var(--eerie-black);\n  width: 100px;\n}\n\n.action-btn {\n  background-color: var(--shamrock-green);\n  color: var(--sea-salt);\n}\n\n.action-btn,\n.addTask-btn,\n.cancel-btn,\n#showDoneTasksBtn {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-family: inherit;\n  font-size: 1.8rem;\n  font-weight: 700;\n  cursor: pointer;\n}\n\n.addTask-btn {\n  justify-content: start;\n  background-color: var(--timberwolf);\n  width: 100%;\n  border: none;\n  color: var(--eerie-black);\n  padding: 20px 20px;\n  margin-top: 30px;\n  gap: 15px;\n  border-radius: 5px;\n}\n\n.create-project-btn,\n#showDoneTasksBtn {\n  display: flex;\n  align-items: center;\n  border: none;\n  background-color: transparent;\n  cursor: pointer;\n  color: var(--platinum);\n  gap: 10px;\n  padding: 5px;\n  border-radius: 5px;\n}\n\n#showDoneTasksBtn:hover {\n  background-color: var(--orange);\n  cursor: pointer;\n}\n\n.task-card:hover {\n  background-color: var(--timberwolf);\n  color: var(--eerie-black);\n}\n\n.task-card:hover .task-check {\n  border-color: var(--eerie-black);\n}\n\n.project-card:hover,\n.create-project-btn:hover {\n  background-color: var(--dark-black);\n}\n\n.project-card:hover .edit-btn,\n.task-card:hover .edit-btn {\n  color: #0369a1;\n  cursor: pointer;\n}\n\n.project-card:hover .delete-btn,\n.task-card:hover .delete-btn {\n  color: #dc2626;\n  cursor: pointer;\n}\n\n.edit-btn:hover,\n.delete-btn:hover {\n  scale: 1.1;\n}\n\n.cancel-btn:hover,\n.action-btn:hover {\n  scale: 1.05;\n}\n\n.task-form input[type=\"text\"]:focus,\ntextarea:focus,\nselect:focus {\n  background-color: var(--eerie-black);\n  color: var(--sea-salt);\n}\n\n.dialog-modal::backdrop {\n  backdrop-filter: blur(2px);\n}\n"],"sourceRoot":""}]);
